.media {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  z-index: 9999;

  &--footer {
    svg {
      fill: #202c47;
    }
  }
}

.icon {
  fill: white;
  width: 34px;
  height: 34px;
  cursor: pointer;
  transition: transform 200ms ease-in-out, fill 200ms ease-in-out;
}

.icon:focus,
.icon:hover {
  fill: rgb(251, 109, 58);
  transform: scale(1.2);
}
